.table {
  width: 100%;
  border: 1px solid black;
  border-collapse: collapse;
  margin-bottom: 1%;
  padding-bottom: 7px; }

.th {
  width: 16%;
  text-align: center; }
  .th:first-child {
    width: 1%;
    padding-left: 5px; }

.campaignHead {
  background-color: #CCCCCC; }

.btn {
  color: white;
  padding: 6px;
  float: right;
  min-width: 90px;
  border: 1px solid black;
  background-color: #2B78E4;
  border-style: none;
  border-radius: 5px; }



