.td {
  width: 16%;
  text-align: center; }
  .td:first-child {
    width: 1%;
    padding-left: 5px; }

.campaignItem {
  line-height: 35px; }
  .campaignItem:nth-child(even) {
    background-color: #EEEEEE; }

.budget {
  border: 1px solid black;
  padding: 4px;
  background-color: transparent;
  width: 5%;
  min-width: 65px; }

.name {
  border: 1px solid black;
  padding: 4px;
  background-color: transparent;
  width: 16%;
  min-width: 150px; }

.status {
  width: 5%;
  min-width: 76px;
  background-image: linear-gradient(45deg, transparent 50%, #DDDDDD 50%), linear-gradient(135deg, #DDDDDD 50%, transparent 50%), linear-gradient(to right, #DDDDDD, #DDDDDD);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), 100% 0;
  background-size: 0px 0px, 0px 0px, 1.5em 2.5em;
  background-repeat: no-repeat; }

.date {
  display: inline-block; }
  .date input {
    width: 50%;
    height: 23px;
    min-width: 87px;
    border: 1px solid black;
    background-image: url("/images/cal.png");
    background-repeat: no-repeat;
    background-position-x: 64px; }



