.header {
  background-color: #DDDDDD;
  text-align: center;
  height: 10%;
  min-height: 57px;
  max-height: 62px; }

.appName {
  font-weight: 500;
  height: 50%; }

.bar {
  height: 50%;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  justify-content: space-around;
  padding-top: 3px;
  border-top: 1px solid #666666;
  border-bottom: 1px solid #666666; }

.nav {
  display: flex;
  flex-basis: 6%;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 2px; }

.search {
  flex-basis: 88%;
  margin-top: 1px; }
  .search input {
    width: 100%;
    height: 75%;
    padding-left: 20px;
    box-sizing: border-box;
    background-image: url("/images/sprite.png");
    background-repeat: no-repeat;
    background-position-x: 0px;
    background-position-y: -59px; }

.back {
  height: 18px;
  width: 19px;
  background-image: url("/images/sprite.png");
  background-position: 0px 0px; }

.foward {
  height: 18px;
  width: 19px;
  background-image: url("/images/sprite.png");
  background-position: 0px -19px; }

.refresh {
  height: 18px;
  width: 19px;
  background-image: url("/images/sprite.png");
  background-position: 0px -38px; }

.menu {
  height: 18px;
  width: 19px;
  background-image: url("/images/sprite.png");
  background-position: 0px -77px; }



